<template>
  <div id="classificationpie" style="height:380px">

  </div>
</template>
<script>
  export default {
    props: ['conventNum', 'mslNum', 'esdNum', 'locationSum'],
    data() {
      return {
        pieArr: [],
      }
    },
    mounted() {
      this.pieFunction()
    },
    watch: {
      used(value) {
        this.used = value
      },
      free(value) {
        this.free = value
      }
    },
    methods: {
      pieFunction() {
        // console.log(this.used)
        this.pieArr = [{
          'name': this.$t('dashboard.convent_num'),
          'value': this.conventNum
        }, {
          'name': this.$t('dashboard.msl_num'),
          'value': this.mslNum
        }, {
          'name': this.$t('dashboard.esd_num'),
          'value': this.esdNum
        }];
        var myPie = this.$echarts.init(document.getElementById('classificationpie'), 'macarons');
        // 饼状图配置数据
        var option = {
          title: {
            text: this.$t('dashboard.storageType'),
            subtext: this.$t('dashboard.storageTotal') + (this.locationSum),
            x: 'center',
            textStyle: {
              // 　　　　		fontSize:14
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 20,
            top: 20,
            data: this.nameArr
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          calculable: true,
          series: [{
            name: this.$t('dashboard.percent'),
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: this.pieArr,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  formatter: '{c}{b} ({d}%)'
                },
                labelLine: {
                  show: true
                }
              }
            }
          }],
        };
        myPie.setOption(option);
        window.addEventListener("resize", function() {
          myPie.resize();
        });
      }
    }

  }
</script>



