<template>
  <div class="dashboard_content">
    <!-- 标题栏 -->
    <div class="dashboard_title">
      <span class="dashboard_welcome">
        <span>{{$t('dashboard.welcomeTo')}}</span>
        <span>Swissmic WMS</span>
      </span>
      <span class="dashboard_opt_btn">
        <el-button type="success" size="mini" @click="downLoad" v-has="3">
          {{$t('dashboard.downloadExcel')}}
          <i class="el-icon-download el-icon--right"></i>
        </el-button>
      </span>
    </div>
    <!-- 日期 -->
    <div class="dashDate">
      <span>{{this.year}}/</span>
      <span>{{this.month}}/</span>
      <span>{{this.day}}</span>
      <span>{{$t('dashboard.dataInfo')}}</span>
    </div>
    <div class="charts">
      <!--存储和物料统计  -->
      <el-row>
        <el-col :span="12">
          <div class="storage_pie" v-if="location_sum">
            <v-storagepie :usedNum="used_num" :freeNum="free_num" :locationSum="location_sum"></v-storagepie>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="frequency" v-if="location_sum">
            <v-classification :conventNum="convent_num" :mslNum="msl_num" :esdNum="esd_num" :locationSum="location_sum"></v-classification>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="charts">
      <!--出料/入料/告警统计  -->
      <el-row>
        <el-col :span="12">
          <div class="storage" v-if="mate_in_count.length">
            <v-inandout :incount="mate_in_count" :outcount="mate_out_count"></v-inandout>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="frequency" v-if="warning_count.length">
            <v-warning :warningcount="warning_count"></v-warning>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
  import VStoragepie from './storagepie' //存储孔位
  import VClassification from './classification' //物料分类
  import VInandout from './inandout' //出料/入料
  import VWarning from './warning' //告警统计
  import {
    exportMateInfo,
    report
  } from '@/request/api/material'
  import {
    genUrl,
    localDate
  } from '@/utils/public'
  export default {
    components: {
      VStoragepie,
      VClassification,
      VInandout,
      VWarning
    },
    data() {
      return {
        location_sum: '', //所有孔位
        used_num: '', //已使用
        free_num: '', //空闲
        mate_in_count: [], //月每日上料次数
        mate_out_count: [], //月每日取料次数
        convent_num: '', //常规物料数量
        msl_num: '', //湿敏元器件数量
        esd_num: '', //esd敏感元器件数量
        warning_count: '',
        // flag:false,
        year: localDate().year,
        month: localDate().month,
        day: localDate().day
      }
    },
    mounted() {
      this.getReport()
    },
    methods: {
      getReport() {
        report({}).then(res => {
          if (res.result) {
            this.location_sum = res.location_sum
            this.used_num = res.used_num
            this.free_num = res.free_num
            this.mate_in_count = res.mate_in_count
            this.mate_out_count = res.mate_out_count
            this.convent_num = res.convent_num
            this.msl_num = res.msl_num
            this.esd_num = res.esd_num
            this.warning_count = res.warning_count
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      downLoad() {
        exportMateInfo({}).then(res => {
          const url = genUrl(res, {}); //{}指的是表头，res是后台返回来的数据
          const isIE = false || !!document.documentMode || "ActiveXObject" in window;
          if (isIE) {
            // ie6-11
            navigator.msSaveOrOpenBlob(new Blob([url], {
              type: "text/csv"
            }), '物料信息统计报表.csv')
          } else if (!isIE && !!window.StyleMedia) {
            //edge 20+
            navigator.msSaveOrOpenBlob(new Blob([url], {
              type: "text/csv"
            }), '物料信息统计报表.csv')
          } else {
            //chrome, firefox
            const a = document.createElement('a');
            a.href = url;
            a.download = "物料信息统计报表.csv";
            a.click();
            window.URL.revokeObjectURL(url);
          }
        });
      }
    }
  }
</script>
<style>
  .dashboard_title {
    display: flex;
    /*height: 60px;
		line-height: 60px;*/
  }

  .dashboard_welcome {
    flex: 1;
    font-size: 20px;
    height: 60px;
    line-height: 60px;
    color: #007BFF;
  }

  .dashboard_opt_btn {
    line-height: 60px;
    width: 120px;
    margin-right: 20px;
  }

  .dashboard_title .el-dialog__body {
    padding: 0 20px;
  }

  .el-upload__tip {
    height: 36px;
    line-height: 36px;
  }

  .dashDate {
    color: #999999;
    font-size: 20px;
  }

  .charts {
    /*display: flex;*/
  }

  .storage {
    /*width:100%;*/
  }

  .storage,
  .frequency {
    border: 1px solid #999999;
    /*width:100%;*/
    margin: 2%;
    border-radius: 4px;
    background: #f3f3f3;
    padding: 20px;
  }

  .storage_pie {
    border: 1px solid #999999;
    margin: 2%;
    border-radius: 4px;
    background: #f3f3f3;
    padding: 20px;
  }
</style>



